@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap");
/* @tailwind base; */
@tailwind components;
@tailwind utilities;

a {
  text-decoration: none;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.6rem;
}

/* bars button */
.bars {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 16.5px;
  width: 25px;
}
.bars .bar {
  width: 100%;
  height: 2px;
}

/* buttons */
.btn {
  font-size: 14.5px;
  font-weight: 600;
  padding: 1.4rem 1.6rem;
  border-radius: 4px;
  display: inline-block;
}

.btn-primary {
  transition: var(--transition);
}

.btn-primary:hover {
  background-color: transparent;
}

.btn-secondary {
  background-color: transparent;
  transition: var(--transition);
}

.btn-group button:first-child,
.btn-group a:first-child {
  margin-right: 1rem !important;
}

/* navbar part */
.navbar {
  height: 80px;
  display: flex;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 3px 8px;
}

.navbar .container {
  width: 100%;
}

.navbar-brand {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 1.8rem;
}
.navbar-brand-text {
  font-weight: 600;
}

.navbar-brand-icon {
  width: 25px;
  margin-right: 6px;
  opacity: 0.8;
}
.brand-and-toggler {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header {
  min-height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.header-content {
  max-width: 740px;
  margin-right: auto;
  margin-left: auto;
}
.header-content img {
  max-width: 760px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  margin-top: 3.2rem;
}
.lg-title {
  margin: 1.4rem 0;
  font-size: 37px;
  line-height: 1.4;
}
.header-content p {
  margin-bottom: 2.6rem;
  line-height: 1.6;
}

/* section one */
.section-one {
  padding: 64px 0;
  min-height: 80vh;
  display: flex;
  align-items: center;
}
.section-one-l img {
  max-width: 545px;
  margin-right: auto;
  margin-left: auto;
}
.section-one-r {
  margin-top: 4rem;
}

.section-one .btn-group {
  margin-top: 2rem;
}
.section-one-r {
  max-width: 545px;
  margin-right: auto;
  margin-left: auto;
}
.section-one-r .btn-group {
  margin-top: 3rem;
}

/* section two */
.section-two {
  padding: 64px 0;
}
.section-two .section-items {
  display: grid;
  gap: 2rem;
}

.section-two .section-item {
  max-width: 350px;
  text-align: center;
  margin-right: auto;
  margin-left: auto;
}
.section-two .section-item-icon {
  margin: 1rem 0;
}
.section-two .section-item-icon img {
  width: 80px;
  margin-right: auto;
  margin-left: auto;
}
.section-two .section-item-title {
  font-size: 1.8rem;
  font-weight: 600;
}
.section-two .text {
  margin: 0.9rem 0;
}

/* footer */
.footer {
  padding: 3rem 0;
}

/* media queries */
@media screen and (min-width: 768px) {
  .section-two .section-items {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 992px) {
  .section-one-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem;
  }
  .section-one-r {
    text-align: left;
  }
  .section-two .section-items {
    grid-template-columns: repeat(3, 1fr);
  }
  .section-two .section-item {
    text-align: left;
  }
  .section-two .section-item-icon img {
    margin-left: 0;
  }
}

/* resume page */
#about-sc {
  padding: 64px 0;
}

.cv-form-row-title {
  padding: 0.8rem 1.6rem;
  margin-bottom: 2rem;
}

.cv-form-row-title h3 {
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-size: 18px;
}

.cv-form-row {
  padding: 3rem 2rem 0 2rem;
  border: 1px solid rgba(0, 0, 0, 0.98);
  margin-bottom: 1rem;
  position: relative;
}
textarea {
  resize: none;
}
.form-elem {
  margin-bottom: 3rem;
  position: relative;
}
.form-label {
  display: block;
  font-weight: 600;
  font-size: 10px;
  margin-bottom: 0.5rem;
}
.form-control {
  border-radius: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 10px;
  padding: 4px 8px;
  font-family: inherit;
  width: 100%;
  outline: 0;
  transition: var(--transition);
}

.form-control:focus {
  border-color: rgba(0, 0, 0, 0.3);
}
.form-text {
  color: #ca0b00;
  font-size: 12px;
  position: absolute;
  letter-spacing: 0.5px;
  top: calc(100% + 2px);
  left: 0;
  width: 100%;
}
.cols-3,
.cols-2 {
  display: grid;
}

.repeater-add-btn {
  @apply w-6 h-6 flex justify-center items-center rounded-full p-1 text-white  bg-blue-600;
}

/* preview section */
.preview-cnt {
  border-radius: 5px;
  display: grid;
  grid-template-columns: 32% auto;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  overflow: hidden;
}

.preview-cnt-l {
  padding: 3rem 3rem 2rem 3rem;
}
.preview-cnt-r {
  padding: 3rem 3rem 3rem 4rem;
}
.preview-cnt-l .preview-blk:nth-child(1) {
  text-align: center;
}
.preview-image {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: auto;
  margin-left: auto;
}
.preview-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.preview-item-name {
  font-size: 2.4rem;
  font-weight: 600;
  margin: 1.8rem 0;
  position: relative;
}
.preview-item-name::after {
  position: absolute;
  content: "";
  bottom: -10px;
  width: 50px;
  height: 1.5px;
  background-color: rgba(255, 255, 255, 0.5);
  left: 50%;
  transform: translateX(-50%);
}
.preview-blk {
  padding: 1rem 0;
  margin-bottom: 1rem;
}
.preview-blk-title h3 {
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.08);
  padding-bottom: 0.5rem;
}
.preview-blk-title {
  margin-bottom: 1rem;
}
.preview-blk-list .preview-item {
  font-size: 1.5rem;
  margin-bottom: 0.2rem;
  opacity: 0.95;
}
.preview-cnt-r .preview-blk-list .preview-item {
  margin-top: 1.8rem;
}

.achievements-items.preview-blk-list .preview-item span:first-child,
.educations-items.preview-blk-list .preview-item span:first-child,
.experiences-items.preview-blk-list .preview-item span:first-child {
  display: block;
  font-weight: 600;
  margin-bottom: 1rem;
  background-color: rgba(0, 0, 0, 0.03);
}

.educations-items.preview-blk-list .preview-item span:nth-child(2),
.experiences-items.preview-blk-list .preview-item span:nth-child(2) {
  font-weight: 600;
  margin-right: 1rem;
}

.educations-items.preview-blk-list .preview-item span:nth-child(3),
.experiences-items.preview-blk-list .preview-item span:nth-child(3) {
  font-style: italic;
  margin-right: 1rem;
}

.educations-items.preview-blk-list .preview-item span:nth-child(4),
.educations-items.preview-blk-list .preview-item span:nth-child(5),
.experiences-items.preview-blk-list .preview-item span:nth-child(4),
.experiences-items.preview-blk-list .preview-item span:nth-child(5) {
  margin-right: 1rem;
  background-color: var(--clr-green);
  color: var(--clr-white);
  padding: 0 1rem;
  border-radius: 0.6rem;
}

.educations-items.preview-blk-list .preview-item span:nth-child(6),
.experiences-items.preview-blk-list .preview-item span:nth-child(6) {
  font-size: 13.5px;
  display: block;
  opacity: 0.8;
  margin-top: 1rem;
}
.projects-items.preview-blk-list .preview-item span {
  display: block;
}

@media screen and (min-width: 768px) {
  .cols-3 {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
  }
  .cols-2 {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}

@media screen and (min-width: 992px) {
  .cv-form-row {
    padding: 3rem 3rem 0rem 3rem;
  }
  .cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
}

.print-btn-sc {
  margin: 2rem 0 6rem 0;
}

/* print section */
@media print {
  body * {
    visibility: hidden;
  }

  .non_print_area {
    display: none;
  }

  .print_area,
  .print_area * {
    visibility: visible;
  }

  .print_area {
    width: 100%;
  }
}
.manrope {
  font-family: "Manrope", sans-serif;
}
